import React, { useState, useEffect } from 'react';
import Remark from './Remark';


function TagSelector({ input, setInput, remark, setRemark, setAmount, selectedTag, setSelectedTag, tags, amount }) {
   

    const handleReset = () => {
        setInput('');
        setRemark('');
        setAmount('0');
        // 
    };

    const secondTags = {
        '': ['', '', '', '', '', '', ''],
        '': ['', '', '', '', ''],
        '': ['', '', ''],
        '': ['', '', '', '', '' ,'', ''],
        '': ['', '', ''],
        '': ['', '', '', ''],
        '': ['', '', '' ],
        '': ['', '', ''],
        '': ['', '', '', ''],
        '': ['', ''],
        '': ['', '', ''],
        '': ['', '', ''],
        '': ['', '', ''],
    }
    const handleInputChange = (e) => setInput(e.target.value);
    const selectTag = (tag) => {
        setInput('');
        setRemark('');
        if (selectedTag == tag) {
            setSelectedTag('');
            return;
        }
        setSelectedTag(tag);
    };

    const handleSecondaryTagClick = (tag) => {
        setInput(tag);
        setRemark('');
    };


    return (
        <>
        <div className="input-container grid grid-2">
            <input type="text" value={input} onChange={handleInputChange} placeholder="" />
            <button onClick={handleReset}></button>
        </div>
        <div className="secondary-tags-container">
            {secondTags[selectedTag] ? (
                <ul className="second-tag">
                    {secondTags[selectedTag].map((tag, index) => (
                        <li key={index} onClick={() => handleSecondaryTagClick(tag)}>{tag}</li>
                    ))}
                </ul>
            ) : selectedTag ? (
                <p className="help-message"><b>{selectedTag}</b> ,</p>
            ) : (
                <p className="help-message">Tag</p>
            )}
        </div>
        <div className="tag-container">
            {tags.map(tag => (
                <div key={tag} className={`tag ${selectedTag === tag ? 'selected' : ''}`} onClick={() => selectTag(tag)}>
                    {tag}
                </div>
            ))}
        </div>
        </>
    );
}

export default TagSelector;